<template>
  <div>
    <v-container class="servempresas__container">
      <title-ribbon :title="'Servicios a Empresas y Colegios'" />
      <div class="servempresas_container__main">
        <v-row class="servempresas_row__portal">
          <v-col class="col-9">
            <v-row class="justify-center">
              <v-col class="col-12 col-md-5 srp_title">
                <h4>Ingresa a tu Portal de Empresa:</h4>
              </v-col>
              <v-col class="col-12 col-md-2 srp_input">
                <v-btn
                  color="success"
                  :small="$vuetify.breakpoint.xs"
                  href="https://www.centrodeterapia.cl/empresas/"
                  target="_blank"
                >
                  Ingresar
                </v-btn>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        <v-row class="servempresas_row__img1">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/servicios-empresas/convenio-empresas-atencion-psicologica"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="servempresas_row__header">
          <v-col>
            <p>
              El bienestar y la salud emocional, conductual y familiar son
              factores claves en las personas para su vida personal, familiar,
              académica y laboral. Cualquier alteración de alguno de estos
              aspectos puede afectar su desempeño o comportamiento dentro de una
              organización de la cuál sea parte. Por esto es importante que todo
              tipo de organizaciones implemente medidas de prevención, y
              mejoramiento de la salud mental y emocional para los integrantes
              de su comunidad.
            </p>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="servempresas_row__boxes-title">
          <v-col>
            <h3>
              Nuestros servicios están orientados a los siguientes tipos de
              organizaciones:
            </h3>
          </v-col>
        </v-row>
        <v-row class="servempresas_row__boxes">
          <v-col class="col-12 col-md-6 srb_box__empresas">
            <div class="srbb_empresas__main">
              <div class="srbbe_title">
                <h2>Empresas</h2>
              </div>
              <div class="srbbe_content">
                <div class="srbbe_text">
                  <v-img
                    src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/servicios-empresas/convenio-empresas-psicologo"
                    width="140"
                    max-width="140"
                  />
                  <p>
                    Entregamos apoyo a todo tipo de empresas para mejorar
                    problemas de depresión, estrés, consumo de alcohol y drogas,
                    relaciones interpersonales, comunicación, adaptación a los
                    cambios, tolerancia a la frustración, entre otros temas.
                  </p>
                </div>
              </div>
            </div>
          </v-col>
          <v-col class="col-12 col-md-6 srb_box__colegios">
            <div class="srbb_colegios__main">
              <div class="srbbc_title">
                <h2>Colegios</h2>
              </div>
              <div class="srbbc_content">
                <div class="srbbc_text">
                  <v-img
                    src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/servicios-empresas/convenio-empresas-psicologos"
                    width="140"
                    max-width="140"
                  />
                  <p>
                    Entregamos apoyo a todo tipo de instituciones educacionales
                    para mejorar problemas de aprendizaje, emocionales,
                    sociales, rendimiento escolar, déficit atencional e
                    hiperactividad, conducta, bullying, entre otros temas.
                  </p>
                </div>
              </div>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row>
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/servicios-empresas/charlas-talleres-empresas-psicologia"
            />
          </v-col>
        </v-row>
        <splitter />
        <v-row class="servempresas_row__servicios">
          <v-col>
            <p>
              En
              <strong>Centro de Terapia del Comportamiento</strong> entregamos
              esta ayuda con nuestros más de 300 profesionales de las
              especialidades de Psicología, Psiquiatría, Neurología,
              Fonoaudiología, Psicopedagogía, Nutrición y Terapia Ocupacional, a
              través de los siguientes servicios:
            </p>
            <v-expansion-panels>
              <v-expansion-panel
                v-for="(servicio, s) in serviciosEmpresasItemsComputed"
                :key="s"
                style=""
              >
                <v-expansion-panel-header
                  style="background: transparent; border: none; box-shadow: none;"
                >
                  <v-row class="srs_header_item">
                    <v-col class="col-12 col-md-1 srs_header_item__image">
                      <v-img
                        :src="
                          `https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/servicios-empresas/${servicio.img}`
                        "
                        :max-width="$vuetify.breakpoint.xs ? 40 : 60"
                      />
                    </v-col>
                    <v-col class="col-12 col-md-11 srs_header_item__text">
                      <div class="srs_header_item_text__main">
                        <div>
                          <h3>{{ servicio.title }}</h3>
                        </div>
                        <span> {{ servicio.text }}</span>
                      </div>
                    </v-col>
                  </v-row>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <div
                    v-if="servicio.desc1"
                    class="srs_content__desc1"
                    v-html="servicio.desc1"
                  />
                  <div
                    v-if="servicio.desc2"
                    class="srs_content__desc2"
                    v-html="servicio.desc2"
                  />
                  <div
                    v-if="servicio.list"
                    class="srs_content__list"
                  >
                    <div
                      v-for="(list, l) in servicio.list"
                      :key="l"
                    >
                      <h3>{{ list.listTitle }}</h3>
                      <ul>
                        <li
                          v-for="(listLi, i) in list.listLi"
                          :key="i"
                        >
                          <span>{{ listLi }}</span>
                        </li>
                      </ul>
                    </div>
                  </div>
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="servempresas_row__convenios-title">
          <v-col>
            <h3>
              Algunas de nuestras empresas en convenio:
            </h3>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-carousel
              :continuous="true"
              :cycle="true"
              hide-delimiters
              hide-delimiter-background
              height="200"
            >
              <v-carousel-item
                v-for="(item, i) in slidersEmpresas"
                :key="i"
                :src="
                  $vuetify.breakpoint.xs
                    ? `https://res.cloudinary.com/dkod5pdlc/image/upload/v1632951082/terapiacl/sliders-mobile/empresas/${item.ImagenMobile}`
                    : `https://res.cloudinary.com/dkod5pdlc/image/upload/v1632951082/terapiacl/sliders/empresas/${item.Imagen}`
                "
              />
            </v-carousel>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="servempresas_row__convenios-form">
          <v-col>
            <div class="src-form__main">
              <v-row>
                <v-col>
                  <h2>FORMULARIO DE CONTACTO</h2>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <form @submit.prevent="validateForm()">
                    <validation-observer
                      ref="observer"
                      v-slot="{ invalid }"
                    >
                      <v-row>
                        <v-col>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:250"
                            name="Empresa"
                            class="validation_provider_single"
                          >
                            <label class="item-label"> Empresa: </label>
                            <v-text-field
                              v-model="formulario.empresa"
                              :error-messages="errors"
                              background-color="lightgreen4"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:250"
                            name="Contacto"
                            class="validation_provider_single"
                          >
                            <label class="item-label"> Contacto: </label>
                            <v-text-field
                              v-model="formulario.contacto"
                              :error-messages="errors"
                              background-color="lightgreen4"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>

                      <v-row>
                        <v-col class="validation_provider_doble_container">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|max:50|numeric"
                            name="Telefono"
                            class="validation_provider_doble_right"
                          >
                            <label class="item-label"> Teléfono: </label>
                            <v-text-field
                              v-model="formulario.telefono"
                              :error-messages="errors"
                              background-color="lightgreen4"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                          <validation-provider
                            v-slot="{ errors }"
                            rules="required|email|max:100"
                            name="Email"
                            class="validation_provider_doble"
                          >
                            <label class="item-label"> Email: </label>
                            <v-text-field
                              v-model="formulario.email"
                              :error-messages="errors"
                              background-color="lightgreen4"
                              color="success"
                              filled
                              dense
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col class="text-left">
                          <validation-provider
                            v-slot="{ errors }"
                            name="Servicios"
                            rules="required|max:250"
                            class="validation_provider_normal"
                          >
                            <label class="item-label">
                              Servicio a solicitar (puedes marcar más de una
                              opción):
                            </label>

                            <div class="src-form_main__chk">
                              <v-checkbox
                                v-for="(item, i) in serviciosEmpresasItems"
                                :key="i"
                                v-model="formulario.servicios"
                                class="src-form_main_chk__item"
                                :label="item.shortTitle"
                                color="success"
                                :value="item.shortTitle"
                                hide-details
                                :error-messages="errors"
                                multiple
                              />
                            </div>
                          </validation-provider>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col class="text-left">
                          <validation-provider
                            v-slot="{ errors }"
                            rules="max:500"
                            name="Consulta"
                            class="validation_provider_normal"
                          >
                            <label class="item-label"> Consulta: </label>
                            <v-textarea
                              v-model="formulario.consulta"
                              :error-messages="errors"
                              background-color="lightgreen4"
                              color="success"
                              filled
                              name="input-7-4"
                            />
                          </validation-provider>
                        </v-col>
                      </v-row>
                      <!-- <v-row>
                        <v-col class="frm-alert">
                          <div class="frm-alert__main">
                            <v-alert
                              v-show="showRecaptchaAlert"
                              dense
                              text
                              type="warning"
                              style="padding: 1px; margin: 0px;"
                              class="src-form-alert"
                            >
                              Marque la casilla de verificación
                            </v-alert>
                            <v-alert
                              v-show="showEnviado"
                              dense
                              text
                              type="success"
                              style="padding: 1px; margin: 0px;"
                              class="box"
                            >
                              Formulario Enviado
                            </v-alert>
                          </div>
                        </v-col>
                      </v-row> -->
                      <v-row class="flex-row-reverse">
                        <v-col class="frm-btn col-12 col-md-6 pa-0">
                          <div class="frm-btn__up">
                            <div class="src-form-recaptcha">
                              <vue-recaptcha
                                sitekey="6Lf7HQQeAAAAAC7MOA15SB35MQ8UwUh8wYG5rg-h"
                                @verify="onCaptchaVerify"
                                @expired="onCaptchaExpired"
                              />
                            </div>
                          </div>
                          <div class="frm-btn__bottom">
                            <v-btn
                              color="success"
                              default
                              type="submit"
                              :disabled="invalid"
                              :loading="btnLoader"
                            >
                              Enviar
                            </v-btn>
                            <div class="alert">
                              <v-alert
                                v-show="showRecaptchaAlert"
                                dense
                                text
                                type="warning"
                                style="padding: 1px; margin: 0px;"
                                class="src-form-alert"
                              >
                                Marque la casilla de verificación
                              </v-alert>
                              <v-alert
                                v-show="showEnviado"
                                dense
                                text
                                type="success"
                                style="padding: 1px; margin: 0px;"
                                class="box"
                              >
                                Formulario Enviado
                              </v-alert>
                            </div>
                          </div>
                        </v-col>
                      </v-row>
                    </validation-observer>
                  </form>
                </v-col>
              </v-row>
            </div>
          </v-col>
        </v-row>
        <splitter-volver />
      </div>
    </v-container>
  </div>
</template>
<script>
  import VueRecaptcha from 'vue-recaptcha'
  import Vue from 'vue'
  export default {
    name: 'ServiciosEmpresas',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
      VueRecaptcha,
    },
    data () {
      return {
        serviciosEmpresasItems: require('@/data/serviciosEmpresasItems.json'),
        slidersEmpresas: [],
        btnLoader: false,
        recaptchaVerified: false,
        showRecaptchaAlert: false,
        showEnviado: false,
        formulario: {
          empresa: '',
          contacto: '',
          email: '',
          telefono: '',
          servicios: '',
          consulta: '',
        },
      }
    },
    computed: {
      serviciosEmpresasItemsComputed () {
        const items = this.serviciosEmpresasItems.filter(i => i.visible === 1)
        return items
      },
    },
    watch: {
      recaptchaVerified: function () {
        if (this.recaptchaVerified) {
          this.showRecaptchaAlert = false
        }
      },
    },
    mounted () {
      this.getSlidersEmpresas()
    },
    methods: {
      async validateForm () {
        const isValid = await this.$refs.observer.validate()
        if (!isValid) {
          return false
        }

        if (!this.recaptchaVerified) {
          this.showRecaptchaAlert = true
          return false
        }

        this.btnLoader = true
        const body = this.formulario

        Vue.axios
          .post('/api/formularios/servicios-empresas', body)
          .then(response => {
            // console.log(response)
            this.showHideEnviadoMsg()
            this.limpiarFormulario()
            this.btnLoader = false
          })
          .catch(error => {
            console.log(error.message)
          })
      },
      onCaptchaVerify () {
        if (!this.recaptchaVerified) {
          this.recaptchaVerified = true
        }
      },
      onCaptchaExpired () {
        this.recaptchaVerified = false
      },
      showHideEnviadoMsg () {
        this.showEnviado = true
        setTimeout(() => {
          this.showEnviado = false
        }, 10000)
      },
      limpiarFormulario () {
        this.formulario.empresa = ''
        this.formulario.contacto = ''
        this.formulario.email = ''
        this.formulario.telefono = ''
        this.formulario.servicios = ''
        this.formulario.consulta = ''
        requestAnimationFrame(() => {
          // Reseteamos vee
          this.$refs.observer.reset()
        })
        this.onCaptchaExpired() // Reseteamos captcha
      },
      getSlidersEmpresas () {
        Vue.axios
          .get('/api/sliders-empresas')
          .then(response => {
            this.slidersEmpresas = response.data.result
          })
          .catch(error => {
            console.log(error.message)
          })
      },
    },
    metaInfo: {
      title: 'Servicios a Empresas y Colegios',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
